import React, { useEffect, useState } from "react";

import { Button } from "./index";
import useComponentVisible from "../hooks/useComponentVisible";


const normalizeInput = (value: string, previousValue: string): string => {
  if (!value) return value;

  const currentValue = value.replace(/[^\d]/g, '');
  const cvLength = currentValue.length;

  if (!previousValue || (value.length > previousValue.length)) {
    if (cvLength < 4) return currentValue;
    if (cvLength < 7) return `(${ currentValue.slice(0, 3) }) ${ currentValue.slice(3) }`;

    return `(${ currentValue.slice(0, 3) }) ${ currentValue.slice(3, 6) }-${ currentValue.slice(6, 10) }`;
  }

  return currentValue;
};

const validateInput = (value: string): string => {
  if (!value) return "–í–≤–µ–¥–∏—Ç–µ —Å–≤–æ–π —Ç–µ–ª–µ—Ñ–æ–Ω"
  if (value.length !== 14) return "–ù–µ –≤–µ—Ä–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –≤–≤–æ–¥–∞: (555) 555-5555";

  return "";
};


interface IFormOrderPopup {
  myRef: React.LegacyRef<HTMLDivElement>,
  isVisibleForm: boolean,
  onVisibleForm: () => void,
  setPhoneOrder: (phone: string) => void
}


const FormOrderPopup = React.memo((props: IFormOrderPopup) => {
  const { myRef, isVisibleForm, onVisibleForm, setPhoneOrder } = props;

  const [ phone, setPhone ] = useState("");
  const [ error, setError ] = useState("");



  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value: string = event.target.value;

    setPhone(normalizeInput(value, phone));
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const errorInput = validateInput(phone);

    setError(errorInput);

    if(!errorInput) {
      setPhoneOrder(phone);
      onVisibleForm();
      handleReset();
    }
  }

  const handleReset = () => {
    setPhone("");
    setError("");
  };


  return(
    <div
      ref={ myRef }
      className={ "form-order " + ((isVisibleForm) ? "" : "form-order__hide") }
    >
      <span className="form-order--close" onClick={ () => onVisibleForm() }>√ó</span>
      <form className="form-order--form" onSubmit={ handleSubmit }>
          <p className="form-order--label">–î–ª—è –∑–∞–∫–∞–∑–∞: –≤–≤–µ–¥–∏—Ç–µ —Å–≤–æ–π —Ç–µ–ª–µ—Ñ–æ–Ω –∏ –º—ã –≤–∞–º –ø–µ—Ä–µ–∑–≤–æ–Ω–∏–º</p>
          <input
            className="form-order--input"
            type="text"
            name="phone"
            placeholder="(xxx) xxx-xxxx"
            value={ phone }
            onChange={ handleChange }
          />
          { error && <p className="form-order--error">{ error }</p> }

        <div className="form-order--btn-container">
          <Button onClick={ handleReset }>–°–±—Ä–æ—Å–∏—Ç—å</Button>
          <Button className="form-order--primary">–û—Ç–ø—Ä–∞–≤–∏—Ç—å</Button>
        </div>
      </form>
    </div>
  );
})



export default FormOrderPopup;
